<template>
  <div class="add">
    <el-dialog :title="info.title" :visible.sync="info.show">
      <el-form :model="form">
        <el-form-item label="菜单名称" :label-width="formLabelWidth">
          <el-input v-model="form.title"></el-input>
        </el-form-item>
        <el-form-item label="上级菜单" :label-width="formLabelWidth">
          <el-select v-model="form.pid">
            <el-option label="" value="" disabled>--请选择--</el-option>
            <!-- value值为变量 -->
            <el-option label="顶级菜单" :value="0">顶级菜单</el-option>
            <el-option v-for="item in list" :key="item.id" :label="item.title" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="菜单类型" :label-width="formLabelWidth">
          <el-radio v-model="form.type" :label="1">目录</el-radio>
          <el-radio v-model="form.type" :label="2">菜单</el-radio>
        </el-form-item>
        <el-form-item label="菜单图标" :label-width="formLabelWidth" v-if="form.type==1">
          <el-select v-model="form.icon">
            <el-option label="" value="" disabled>--请选择--</el-option>
            <el-option
              v-for="item in icons"
              :key="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="菜单地址" :label-width="formLabelWidth" v-else>
          <el-select v-model="form.url">
            <el-option label="" value="" disabled>--请选择--</el-option>
            <el-option
              v-for="item in urls"
              :key="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="菜单状态" :label-width="formLabelWidth">
          <el-switch
            v-model="form.status"
            active-color="blue"
            inactive-color="grey"
            :active-value="1"
            :inactive-value="2"
          >
          </el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="confirm" v-if="info.isAdd">确 定</el-button>
        <el-button type="primary" @click="update" v-else>修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {addMenu,getMenu,editMenu} from '../../../utils/request'
import { successAlert } from '../../../utils/alert';
import {mapGetters,mapActions} from 'vuex'
export default {
  props: ["info"],
  data() {
    return {
      form: {
        pid: 0, //0表示顶级菜单
        title: "",
        icon: "",
        type: 1, //1目录 2菜单
        url: "",
        status: 1 //1正常 1禁用
      },
      formLabelWidth: "120px",
      icons: [
        "el-icon-setting",
        "el-icon-help",
        "el-icon-s-home",
        "el-icon-s-data"
      ],
      urls: ["menu", "role", "manager",'cate','spec','goods','member','banner','seckill']
    };
  },
  computed:{
    ...mapGetters({
      'list':'menu/menuList'
    })
  },
  methods: {
    ...mapActions({
      'requestMenuList':'menu/menuListActions'
    }),
    cancel() {
      // 这里采用子变,父变的对象形式
      this.info.show = false;
      // 取消时,清空表单数据
      this.form = {
        pid: 0, //0表示顶级菜单
        title: "",
        icon: "",
        type: 1, //1目录 2菜单
        url: "",
        status: 1 //1正常 1禁用
      }
    },
    confirm() {
      // this.cancel();
      // 添加数据
      addMenu(this.form).then(res=>{
        successAlert(res.data.msg);
        this.cancel()
        this.requestMenuList()
      })
    },
    getDetail(id){
      //获取详情
      getMenu({id:id}).then(res=>{
        this.form = res.data.list;
        this.form.id = id;
      })
    },
    update(){
      // 修改
      console.log(this.form);
      editMenu(this.form).then(res=>{
        successAlert(res.data.msg);
        this.cancel(),
        this.requestMenuList()
      })
    }
  },
  mounted(){
    this.requestMenuList()
  }
};
</script>

<style></style>



